import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Quota.css';

const TOTAL_WIDTH = 250;

class Quota extends Component {

    render = () => {
        const width = {
            usage: this.calcWidth(this.props.usage),
            usageInDrive: this.calcWidth(this.props.usageInDrive),
            usageInDriveTrash: this.calcWidth(this.props.usageInDriveTrash),
            free: this.calcWidth(this.props.limit - this.props.usage),
        }

        return <div className="quota">
            <div className="used" style={{width: width.usage}}></div>
            <div className="free" style={{width: width.free}}></div>
            <div className="trash" style={{width: width.usageInDriveTrash}}></div>
            <button onClick={this.props.onEmptyTrash}>Empty trash</button>
        </div>;
    }

    calcWidth = (num) => {
        return Number(num) * TOTAL_WIDTH / Number(this.props.limit);
    }
}

Quota.propTypes = {
    limit: PropTypes.string.isRequired,
    usage: PropTypes.string.isRequired,
    usageInDrive: PropTypes.string.isRequired,
    usageInDriveTrash: PropTypes.string.isRequired,
    onEmptyTrash: PropTypes.func.isRequired
}

export default Quota;


