import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Tags.css';
import ACTIONS from '../../../actions';

class Tags extends Component {
    constructor(props) {
        super(props);
        this.tags = [];
        this.createTags(props);
    }

    render() {
        const tags = [];
        const tagClasses = `tag ${this.props.isClickable ? 'clickable' : ''}`;

        for (let tag of this.tags) {
            const removeButton = this.props.isRemoveable &&
                <button onClick={this.onRemove} tag={tag}>X</button>;
            tags.push(
                <span className={tagClasses} key={`tag_${tags.length}`} onClick={this.onClick} tag={tag}>
                    {tag}
                    {removeButton}
                </span>);
        }
        return <div className="tags">{tags}</div>
    }

    onClick = (event) => {
        if (this.props.isClickable) {
            const tag = event.currentTarget.getAttribute('tag');
            const { store } = this.context;

            store.dispatch(ACTIONS.tag.click(tag));
        }
    }

    onRemove = (event) => {
        const tag = event.currentTarget.getAttribute('tag');
        this.props.onRemoveTag(tag);
    }

    createTags = (props) => {
        if (props.fileName) {
            this.tags = [];
            this.newTags = props.fileName.split(/[\s-]/);

            for (let tag of this.newTags) {
                if (!this.tags.includes(tag)) {
                    this.tags.push(tag);
                }
            }
        }
    }

    componentWillReceiveProps = (newProps) => {
        this.createTags(newProps);
    }
}

Tags.contextTypes = {
    store: PropTypes.object
};

Tags.propTypes = {
    isClickable: PropTypes.bool.isRequired,
    isRemoveable: PropTypes.bool.isRequired,
    onRemoveTag: PropTypes.func,
    fileName: PropTypes.string.isRequired
}

export default Tags;


