import React, { Component } from 'react';
import './About.css';
import service from './service';
import Quota from './Quota';

export default class About extends Component {

    constructor(props) {
        super(props);
        this.state = {
            user: {
                displayName: '',
                emailAddress: '',
                photoLink: ''
            },
            storageQuota: null
        }
    }

    componentDidMount = () => {
        this.loadAbout();
    }

    render() {
        let image = '', quota = '';
        if (this.state.user.photoLink) {
            image = <img className="about__profile-image--rounded"
                src={this.state.user.photoLink} alt="user" />;
        }
        if (this.state.storageQuota) {
            quota = <Quota {...this.state.storageQuota} onEmptyTrash={this.onEmptyTrash}/>;
        }
        return <div className="about">
            <div className="user">
                <div className="about__profile-image">{image}</div>
                <div className="about__display-name">{this.state.user.displayName}</div>
            </div>
            {quota}       
        </div>
    }

    onEmptyTrash = () => {
        service.emptyTrash().then(()=>{
            this.loadAbout();
        });
    }

    loadAbout = () => {
        service.about().then((resp) => {
            this.setState(resp.result);
        }).catch(() => { debugger; });
    }
}


