
export const setName = (file) => ({
    type: 'SET_NAME',
    file
});

export const select = (file) => ({
    type: 'SELECT',
    file
});

export const deleteFile = (file) => ({
    type: 'DELETE',
    file
});

export const clear = (file) => ({
    type: 'CLEAR',
    file
});


