import React, { Component } from 'react';
import ListFiles from './drive/ListFiles';
import Search from './search/Search';
import PropTypes from 'prop-types';
import ACTIONS from '../actions';

class SearchFiles extends Component {
    constructor(props) {
        super(props);
        this.state = { searchTerm: '' };
    }

    componentDidMount = () => {
        const { store } = this.context;

        store.subscribe(this.onAddTag);
    }

    render = () => {
        return <div><Search clear={this.clear} getMatches={this.filter} searchTerm={this.state.searchTerm}/>
            <ListFiles searchTerm={this.state.searchTerm}>
            </ListFiles></div>;
    }

    onAddTag = () => {
        const { store } = this.context;
        const tags = this.state.searchTerm.split(' ');
        const clickedTag = store.getState().tags.clicked;

        if (clickedTag && !tags.includes(clickedTag)) {
            tags.push(clickedTag);
            this.setState({ searchTerm: tags.join(' ') });
            store.dispatch(ACTIONS.tag.clear());
        }
    }

    clear = () => {
        if (this.state.searchTerm !== '') {
            this.setState({ searchTerm: '' });
        }
    }

    filter = (criteria) => {
        if (this.state.searchTerm !== criteria) {
            this.setState({ searchTerm: criteria });
        }
    }

}

SearchFiles.contextTypes = {
    store: PropTypes.object
}

export default SearchFiles;


