
export default (state = {}, action = {}) => {
    const newState = Object.assign({}, state);
    if (action.type) {
        const { tag } = action;
        switch (action.type) {
            case 'CLICK':
                newState.clicked = tag;
                break;
            case 'CLEAR':
                delete(newState.clicked);
                break;
            default:
        }
    }

    return newState;
};


